@import url(https://fonts.googleapis.com/css?family=Blinker&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body, body button {
  font-family: 'Blinker', sans-serif;
}
header {
  height: 60px;
  background-color: #343a40;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  padding-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container h1 {
  margin: 0;
  color: #fff;
}

.container a {
  margin-top: 20px;
  width: 500px;
  height: 50px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: 2px solid #007bff;
  border-radius: 25px;
  cursor: pointer;
  -webkit-transition: background-color .2s;
  transition: background-color .2s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container a:hover {
  background-color: #007bff;
}
.background {
  background-image: url(/react-basico/static/media/banner1.99fa9647.jpg);
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: bottom;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card {
  width: 280px;
  padding: 20px 10px;
  margin: 20px;
  background-color: #d8d8d8;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card .header {
  margin-bottom: 10px;
}

.card p {
  opacity: 0.7;
}

.card a {
  margin-top: 20px;
  width: 100px;
  height: 30px;
  background-color: transparent;
  color: #000;
  text-decoration: none;
  border: 2px solid #007bff;
  border-radius: 25px;
  cursor: pointer;
  -webkit-transition: background-color .1s;
  transition: background-color .1s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card a:hover {
  background-color: #007bff;
  color: #fff;
}
.list-container {
  width: 100%;
  margin: 40px 0 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list-container ul {
  width: 80%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.list-container ul li {
  list-style: none;
}
